.videochat {
  &__heading {
    margin-bottom: 34px;

    span {
      margin-left: 10px;
    }
  }

  &__session-info {
    height: 400px;
  }

  &__buttons-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 24px;

    &:not(.unscheduled) {
      #schedule-session {
        display: none;
      }
    }

    &.unscheduled {

      #cancel-session,
      #reschedule-session {
        display: none;
      }

      #schedule-session {
        display: inline-flex;
      }
    }
  }

  &__btn {
    padding: 8px 24px;

    svg {
      margin-right: 8px;
    }
  }
}

.videochat__user-photo-wrapper {
  position: relative;

  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 50%;
    width: 48px;
    height: 48px;
    background: url('../../img/svg/video-icon.svg') center no-repeat;
  }
}

.videochat__user-photo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.videochat__client-name {
  font-weight: 700;
  font-size: 35px;
}

.videochat__client-id {
  border-radius: 8px;
  padding: 4px;
  background-color: #EAEAEA;
  font-size: 18px;
  color: #777777;
}
